.navbarDiv {
  background-color: rgb(33, 33, 33);
  color: white;
  display: flex;
  justify-content: space-between;
  padding: -10px;
  font-family: sans-serif;
}

a {
  text-decoration: none;
  color: white;
  margin-top: 15px;
  padding-right: 10px;
}
#navText {
  font-size: 20px;
  margin-right: auto;
}
a:hover {
  transition: 0.6s;
  transform: scale(1.1);
  color: #07a8d0;
}
#mylogo {
  height: 3rem;
  width: 3rem;
  border-radius: 50%;
  padding-bottom: 10px;
  padding-left: 10px;
}
